<template>
  <div class="components-container">
    <code>Markdown  <a href="https://github.com/sparksuite/simplemde-markdown-editor" target="_blank">simplemde-markdown-editor</a> vue<a target='_blank' href='https://segmentfault.com/a/1190000009762198#articleHeader14'>  </a></code>
    <div class="editor-container">
      <MdEditor id='contentEditor' ref="contentEditor" v-model='content' :height="300" :zIndex='20'></MdEditor>
    </div>
    <el-button @click='markdown2Html' style="margin-top:80px;" type="primary">HTML<i class="el-icon-document el-icon--right"></i></el-button>
    <div v-html="html"></div>
  </div>
</template>

<script>
    import MdEditor from 'components/MdEditor';
    export default {
      components: { MdEditor },
      data() {
        return {
          content: '## Simplemde',
          html: ''
        }
      },
      methods: {
        markdown2Html() {
          import('showdown').then(showdown => {
            const converter = new showdown.Converter();
            this.html = converter.makeHtml(this.content)
          })
        }
      }
    };
</script>





